.app-container {
  text-align: center;
  height: 100vh;
  background-color: #131c21;
  display: flex;
  align-items: center;
  justify-content: center;
}

.login-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.input-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.input-container input {
  margin: 20px;
  height: 50px;
  font-size: 20px;
  outline: none;
  background: transparent;
  border: none;
  border-bottom: 2px solid rgba(241, 241, 242, 0.63);
  color: #ffffff;
}

.login-button {
  background: #128C7E;
  color: #ffffff;
  font-size: 20px;
  width: 150px;
  padding: 10px;
  border: none;
  border-radius: 25px;
  margin: 20px;
  outline: none;
}

.chat-container {
  height: 100vh;
  background-color: #0c1417;
}

.chat-header {
  height: 10vh;
  text-align: center;
  font-size: 65px;
}

.chat-messages {
  height: 90vh;
  background-color: #0c1417;
  overflow-y: auto;
  overflow-x: hidden;
  display: flex;
  flex-direction: column-reverse;
}

.chat-messages .time-bubble {
  background: #1e2a30;
  display: inline-block;
  border-radius: 3px;
  color: #ffffff;
  margin-left: auto;
  margin-right: auto;
  padding: 0 5px
}

.chat-input {
  height: 10vh;
  background: #1f2428;
  display: flex;
  position: relative;
}

.chat-input input {
  outline: none;
  background: #33383b;
  padding: 10px;
  border: none;
  flex: 7;
  margin: 10px;
  border-radius: 40px;
  color: #ffffff;
  font-size: 15px;
  padding-left: 60px;
}

.chat-input input[type='file'] {
  display: none;
}

.chat-input .attachment {
  position: absolute;
  transform: translateY(-50%);
  top: 50%;
  font-size: 27px;
  left: 32px;
  color: #ffffff;
}

.chat-send {
  flex: 1;
  outline: none;
  border: none;
  background: #1f2428;
  color: #787c7f;
}

.message-bubble {
  margin: 5px 10px;
}

.message-bubble .message {
  color: #282c34;
  padding: 9px;
  border-radius: 10px;
  color: #ffffff;
  display: inline-block;
  max-width: 85%;
  word-break: break-word;
}

.chatImage {
  width: 100%;
}

.message-bubble .time {
  color: #ffffff;
  font-size: 8px;
  margin-top: 3px;
}

.right {
  text-align: right;
}

.left {
  text-align: left;
}

.me span {
  background: #066162;
}

.them span {
  background: #252d30;
}