.progressContainer {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.5);
    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: #ffffff;
}

.progressContainer .progressTitle {
    font-size: 20px;
    margin-bottom: 10px;
}

.progressContainer .progressPercentage {
    font-size: 24px;
    font-weight: bold;
    animation: heartbeat 1.5s infinite linear;
}

@keyframes heartbeat {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.4);
    }
    100% {
        transform: scale(1);
    }
}